.Card {
	margin: 0 0;
}

.Book {
	height: 40vw;
	width: 25vw;
	background-size: 105%;
	background-position: center;
	transition: 0.3s;
}

.read {
	filter: opacity(35%);
}

.banner {
	font-size: 1em;
	font-weight: 800;
	position: absolute;
	width: 25vw;
	word-wrap: all;
	color: black;
	text-align: center;
	vertical-align: middle;
	padding: .5vw 0;
}

.now {
	background-color: #87db42;
}

.soon {
	background-color: orange;
	transition: 0.3s;
}


:hover {

	background-size: 125%;
}


@media only screen and (max-width: 700px) {
  .Book {
    height: 80vw;
    width: 50vw;
  }

  .banner {
  	width: 50vw;
  }
}